import React from "react";
import HomePageNavItem from "./HomePageNavItem";

const HomePageNav = () => {
  return (
    <div className="flex justify-center">
      <ul className="grid  grid-cols-4 justify-center z-[10] text-[11px] sm:text-sm md:text-base whitespace-nowrap ">
        <HomePageNavItem to="/ourPrograms" title="Our Programs" />
        <HomePageNavItem to="/ourServices" title="Our Services" />
        <HomePageNavItem to="/aboutUs" title="About Us" />
        <HomePageNavItem to="/contactUS" title="Contact Us" />
      </ul>
    </div>
  );
};

export default HomePageNav;
