import React, { useEffect } from "react";
import { useState } from "react";
import { NavLink, useLocation, Link } from "react-router-dom";

const HomePageNavItem = ({ title, to }) => {
  const [isActive, setIsActive] = useState(false);
  const location = useLocation();

  useEffect(() => {
    if (location.pathname === to) setIsActive(true);
    else setIsActive(false)
  }, [location]);
  return (
    <Link
      to={to}
      className={`cursor-pointer text-center py-[10px] px-[2px] xs:px-2 xs:py-4 sm:p-4 font-semibold  hover:text-two  relative group transition-all duration-500 ${
        isActive ? "bg-five text-two" : "text-five"
      }`}
    >
      <div
        className={`transition-all duration-500 absolute bottom-0 left-0 bg-two group-hover:w-full h-1 ${
          isActive ? "w-full " : " w-0"
        }`}
      />
      {title}
    </Link>
  );
};

export default HomePageNavItem;
