export default function CarosalOurServices() {
  var settings = {
    dots: true,
    infinite: true,
    speed: 1000,
    slidesToShow: 1,
    slidesToScroll: 1,
    appendDots: (dots) => {
      return (
        <ul>
          {dots.map((item) => (
            <li
              key={item.key}
              onClick={item?.props?.children?.props?.onClick}
              className={`transition-all  duration-1000`}
              style={{
                backgroundColor: item?.props?.className ? "white" : "#b1b1b1",
                width: item?.props?.className ? "30px" : "14px",
                height: "14px",
                borderRadius: "9999px",
              }}
            />
          ))}
        </ul>
      );
    },
    arrows: false,
  };
  return (
    <div className="text-white  md:p-8 p-2 min-w-[310px] w-[83vw] max-w-[700px] leading-8 md:text-xl mx-auto">
      <Slider
        {...settings}
        draggable={false}
      >
        {data?.map((item) => (
          <ArticleContent
            title={item?.title}
            content={item?.content}
            key={item?.id}
          />
        ))}
      </Slider>
    </div>
  );
}
