const ArticleContent = ({ title, content }) => {
  return (
    <div className="flex p-4 text-center flex-col items-center gap-10">
      <div className="text-2xl font-bold">{title}</div>
      <div>
        {content?.slice(0, 200)}
        {content?.length > 200 ? "..." : ""}
      </div>
      <button className="py-2 px-4 text-two bg-white border-[3px] border-white transition-all duration-500 font-semibold hover:text-white hover:bg-two rounded-tlbr20 hover:rounded-trbl20">
        Read More
      </button>
    </div>
  );
};

export default ArticleContent;
